
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}


:root {
	--color-orange: #FF5733;
	--color-green: #009122;
	--color-grey-dark: #5f5f5f;
	--color-grey-light: #D9D9D9;
	--color-white: white;
	--color-black: black;
}


/* ############################################################################################## */
/* ############################       BASIC GLOBAL SETTINGS       ############################### */
/* ############################################################################################## */

.headingFont{
  font-size: 25px;
  text-align: center;
}

.subHeading{
  font-size: 20px;
  text-align: center;
}

.medFont{
  font-size: 15px;
}

.smallFont{
  font-size: 12px;
}

.centerTxt{
  text-align: center;
}







/* ############################################################################################## */
/* ############################               NAVBAR              ############################### */
/* ############################################################################################## */


.navbar-container-grid{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0px 10px;
  height: 66px;
  background: var(--color-white);
}

.navbar{
  margin-top: 5px;
}

.navbar-link{
  margin-left: 25px;
  font-size: 15px;
  color: black;
  transition: 0.2s;
  text-decoration: none;
}


.button {
  position: relative;
  border-radius: 3px;
  padding: 8px 10px 8px 10px;
  text-align: center;
  vertical-align: middle;
  background: var(--color-orange);
  border: none;
  outline: 0;
  outline: none !important;
  outline-color: transparent;
  line-height: 24px;
  margin: 15px 0 15px 40px;
  height: 40px;
  width: 80px;
  border-radius: 4px;
  color: white;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 0.4px;
  font-weight: 700;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}

.login-btn{
  margin-right: 30px;
  margin: auto;
}

.zeitLogo{
    height: 48px;
    width: auto;
    vertical-align: middle;
    margin: -5px 25px 0px 25px;
    cursor: pointer;
}

.cartIcon{
  height: 38px;
  width: auto;
  margin-bottom: -14px;
  margin-right: 25px;
}


.navbarRightContainer{
  margin: auto 20px;
}

.cartIconContainer{
  height: 50px;
}


.cartCountContainer{
  background: var(--color-white);
  border-radius: 50%;
  width: 28px;
  height: 28px;
  display: flex;
  position: absolute;
  top: 33px;
  border: 2px solid var(--color-orange);
}


.cartCount{
  color: var(--color-orange);
  margin: auto;
  font-size: 15px;
  font-weight: bold;
}

.navbarRightAuthGrid{
  display: grid;
  grid-template-columns: 1fr 1fr;
}







/* FOOTER */




.footer ul{
  list-style: none;
  /* top right bottom left*/
}

.footer a {
  color: var(--color-white);
}

.footer-lists {
  display: flex;
  justify-content: space-between;
  max-width: 1300px;
  margin: auto;
  padding: 40px;
}

.footer-lists .list-head {
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 5px;
  color: var(--color-orange)
}

.list-head a {
  color: var(--color-orange)
}

.divider {
  width: 100%;
  height: 3px;
  border-top: 1px solid;
  margin: 20px 0;
}

.footer-grid {
  display: grid;
  grid-template-columns: 2fr 4fr;
  gap: 40px;
  /*top right bottom left*/
  width: 100%;
  position: relative;
  z-index: 4;
}

.social-icons img {
  width: 30px;
  height: 30px;
  margin-right: 25px;
}

.copyright {
  position: relative;
  text-align: right;
}

/* FORM DESIGN */

.formInput{
  padding: 10px 20px;
  width: 100%;
  background: rgba(176, 175, 175, 0.543);
  height: 35px;
  border-radius: 8px;
  border: none;
}